    /* 
    *   websocket send message()
    *   http://c1.private.easemob.com/pages/viewpage.action?pageId=12159600
    *  type 21 22 
    */
    _sendRequestMessage:function(type) {

        if(!type) {
            return
        }

        let _this = this;
        const get_admin_memberId = () => {
            let admin_memberId = '';

            for(var key in _this._cacheMembers) {
                let item =  _this._cacheMembers[key];

                if(item.role == emedia.mgr.Role.ADMIN) {
                    admin_memberId = item.id
                    break
                }
            }

            return admin_memberId

        }

        let admin_memberId = get_admin_memberId();
        if(!admin_memberId) {
            console.warn('this confrence have not admin')
            return
        }

        let { _memberId:my_memberId, memName, nickName } = this

        let message = this.newMessage()
        let post_message = {
            op:1002,
            memId : admin_memberId,
            arg: JSON.stringify( { op2:type, memId: my_memberId, memName, nickName:nickName || memName } ),
            tsxTime: new Date().getTime(),
            _reqOps:[`1002${type}`] // entry.js --> emedia.config.baseAcptOps 
        }

        Object.assign(message, post_message)

        this.postMessage(message);

    },
    // 
    requestToTalker:function() {
        this._sendRequestMessage(21)
    },
