    //  type:21 22  result: 0 1
    // session  session
    _send_action_request_message(type, session, result) {

        if(!session || !type) {
            return
        }

        if(result === undefined) {
            return
        }

        let { tsxId, arg } = session;
        let { memId:applicant_memberId } = arg;

        let message = this.newMessage()
        let post_message = {
            op: 1001,
            arg: JSON.stringify({ op2:type, memId:applicant_memberId }),
            memId:applicant_memberId,
            result,
            tsxId,
            _reqOps:[`1002${type}`]
        }

        Object.assign(message, post_message);
        this.postMessage(message);
    },
