function AdminChangeHandle(props) {
    let {
        my_username,
        my_role,
        stream_list,
        user_room
    } = props;

    // 
    if(my_role == 3) {
        const apply_admin = () => {
    
            message.success('');
            emedia.mgr.requestToAdmin(user_room.confrId);
        
        }
        return(
            <div className="admin-change-handle" onClick={apply_admin}></div>
        ) 
    }

    // 
    if(my_role == 7) {

        let admin_number = 0;
        stream_list.map(item => { //2
            if(
                item && 
                item.member && 
                item.member.role == 7 &&
                item.stream.type != emedia.StreamType.DESKTOP
            ) {
                admin_number++
            }
        });

        if( admin_number < 2) {
            return ''
        }

        const apply_talker = async () => {
    
            if(!my_username) {
                console.warn('ApplyAdmin username is required');
                return
            }
    
            let memName = appkey + '_' + my_username;
            try {
                await emedia.mgr.grantRole(user_room, [memName], 3);
                message.success('')
            } catch (error) {
                message.error('')
            }
        
        }
        return(
            <div className="admin-change-handle" onClick={apply_talker}></div>
        )
    }

    return ''
}

