function inviteModal(info) {

    if(!info) {
        return
    }

    if(document.querySelector('#inviteModal')) {
        return
    }
    const div = document.createElement('div');
    div.id = 'inviteModal'
    document.body.appendChild(div);

    function destroy() {
        const unmountResult = ReactDOM.unmountComponentAtNode(div);
        if (unmountResult && div.parentNode) {
          div.parentNode.removeChild(div);
        }
        
    }

    function copy() { // 
        let textarea = document.querySelector('textarea#invite');
        textarea.select();//
        try {
            document.execCommand('copy');
            message.success('')
        } catch (error) {
            message.error('')
        }
    }

    const roomName = info.roomName || '';
    const invitees = info.invitees || ''; //

    let content = invitees + '\r\n' +
    '' + roomName + '\r\n\r\n' + 
    
    ':\r\n' + 
    'https://meeting.easemob.com/invite/index.html?'+
    'roomName='+ encodeURI(roomName) + '&invitees='+ encodeURI(invitees) +'\r\n\r\n' +
    
    'apphttp://www.easemob.com/download/rtc';

    
    ReactDOM.render(
        <div className="inviteModal" >
            <span className="close" onClick={destroy}>x</span>
            <div className="title">{roomName}</div>
            <textarea value={content} readOnly id='invite'></textarea>
            <Button type="primary" onClick={copy}></Button>
        </div>, div)
}

