class Exc_feed extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            visible: false
        }

        this.handle_click = this.handle_click.bind(this)
        this.handleCancel = this.handleCancel.bind(this)
        this.handleOk = this.handleOk.bind(this)
        this.onChange = this.onChange.bind(this)
    }

    handle_click(){
        this.setState({ visible: true })
    }

    handleOk() {
        let { msg } = this.state;


        this.setState({
            confirmLoading: true,
        });
        setTimeout(() => {
            this.setState({
              visible: false,
              confirmLoading: false,
            });

            emedia.fileReport('', msg);

            Modal.success({
                title: '',
                content: `QQ454374569, `,
                okText: ''
            });
        }, 500);
    }

    handleCancel() {
        this.setState({ visible: false })
    }

    onChange(e) {
        let value = e.target.value;

        this.setState({
            msg: value
        })
    }
    render() {

        let {
            visible ,
            confirmLoading
        } = this.state;

        const { TextArea } = Input
        return (
            <div>
                <Modal
                title=""
                visible={visible}
                onOk={this.handleOk}
                confirmLoading={confirmLoading}
                onCancel={this.handleCancel}
                okText=''
                cancelText=''
                >
                    <TextArea rows={4} onChange={this.onChange}/>
                </Modal>
                <div className="exception-feedback-action" onClick={this.handle_click}></div>
            </div>
        )
    }
}
