class ChooseDesktopMedia extends PureComponent {

    state = {
        visible:false,
        sources:[],
        accessApproved: null,
        choosed_stream: null
    }

    show (sources, accessApproved, accessDenied) {

        this.setState({ 
            sources,
            accessApproved,
            accessDenied,
            visible: true 
        })
    }

    hide() {
        this.setState({ 
            visible: false,
            choosed_stream: null 
        });
        
        let { accessDenied } = this.state
        if(
            accessDenied &&
            typeof accessDenied == 'function'
        ) {
            accessDenied()
        }
    }

    // 
    choose(stream) {
        if(!stream) {
            return
        }


        this.setState({
            choosed_stream: stream
        })
    }

    //
    share() {
        let { accessApproved, choosed_stream } = this.state;

        if(
            !accessApproved ||
            !choosed_stream
        ) {
            return
        }
        
        if(typeof accessApproved != 'function'){
            return
        }

        accessApproved(choosed_stream)

        this.setState({
             visible: false,
             choosed_stream: null
        })
    }
    render(){
        let { visible, sources, choosed_stream } = this.state;

        let screen_list = [];
        let window_list = [];

        sources.map(item => { //  
            if(item){
                if(/window/.test(item.id)) {
                    window_list.push(item)
                } else if(/screen/.test(item.id)) {
                    screen_list.push(item)
                }
            }
        })
        const { TabPane } = Tabs;

        return (
            <Modal
                title=""
                visible={visible}
                destroyOnClose={true}
                mask={false}
                maskClosable={false}
                okText=''
                cancelText=''
                closable={false}
                onOk={() => this.share()}
                okButtonProps={{ disabled: !choosed_stream }}
                onCancel={() => this.hide()}
                wrapClassName='electorn-choose-desktop-media'
                getContainer={false}
                width={600}
                style={{ top: 20 }}
            >
                    <div>Electorn </div>

                    <Tabs defaultActiveKey="1">
                        <TabPane tab="" key="1">
                            <div className="tab-content">

                                {
                                    screen_list.map((item, index) => {

                                        let choosed_style = {};

                                        if(choosed_stream){ //
                                            if(choosed_stream.id == item.id) {
                                                choosed_style = {
                                                    borderColor: 'rgba(146, 210, 241, 0.7)'
                                                }
                                            }
                                        }
                                        return (
                                            <div 
                                                className="img-wrapper" 
                                                onClick={() => this.choose(item)}
                                                style={choosed_style}
                                                key={index}
                                            >
                                                <img src={item.hxThumbDataURL} />
                                                <div className='name'> {item.name} </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </TabPane>
                        <TabPane tab="" key="2">
                        <div className="tab-content">
                            {
                                window_list.map((item, index) => {
                                    
                                        let choosed_style = {};


                                        if(choosed_stream){ //
                                            if(choosed_stream.id == item.id) {
                                                choosed_style = {
                                                    borderColor: 'rgba(146, 210, 241, 0.7)'
                                                }
                                            }
                                        }
                                        
                                        return (
                                            <div 
                                                className="img-wrapper" 
                                                onClick={() => this.choose(item)}
                                                style={choosed_style}
                                                key={index}
                                            >
                                            <img src={item.hxThumbDataURL} />
                                            <div className='name'> {item.name} </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        </TabPane>
                    </Tabs>
            </Modal>
        )
    }
}

function getPageQuery(query) { // url
    if(!query) {
        return ''
    }

    let search = window.location.href.split('?')[1];

    if(!search) {
        return ''
    }

    let query_value = '';

    let key_value_arr = search.split('&');
    key_value_arr.map(item => {
        let key = item.split('=')[0];
        let value = item.split('=')[1];

        if(query == key) {
            query_value = value
        }

    })

    return decodeURI(query_value)

}

