    whetherCanUse() {

        if(navigator.userAgent.indexOf('Mobile') > -1) { // 
            this.setState({
                join_btn_disable: true
            })

            return
        }
        if(
            !emedia.isChrome &&
            !emedia.isElectron && 
            !emedia.isEdge && 
            !emedia.isSafari 
        ) {
            this.setState({
                join_btn_disable: true
            })
        }
        

    }
    // join fun start
    async join() {

        this.setState({ loading:true, talker_is_full:false });

        let {
            roomName,
            nickName,
            headimg_url_suffix,
            push_cdn,
            cdn,
            rec, 
            recMerge
        } = this.state;

        let role = this.state.user_room.role;
        
        let params = {
            roomName,
            password: roomName,
            role,
            config:{ 
                nickName,
                ext: {
                    headImage: headimg_url_suffix //
                },
                rec, 
                recMerge,

                // maxTalkerCount:3,//
                // maxVideoCount:2, //
                // maxPubDesktopCount:1 //
            }
        }

        //   cdn
        if(push_cdn && cdn) {

            // let liveCfg = {
            //     cdn,
            //     layoutStyle : 'GRID'
            // }
            // let liveCfg = {
            //     cdn,
            //     layoutStyle : 'CUSTOM',
            //     canvas :{ 
            //         bgclr : 980000,
            //         w : 640,
            //         h : 480
            //     }
            // }
            let { liveCfg } = this.state;
            liveCfg.cdn = cdn;
            params.config.liveCfg = liveCfg
        }

        try {
            const user_room = await emedia.mgr.joinRoom(params);
    
            process.env.NODE_ENV == 'development' ? '' : this.startTime();

            this._set_config_to_localStorage(); 

            this.setState({ 
                joined: true,
                user_room,
            })
    
            window.location.hash = '' //query 

            if(user_room.role == emedia.mgr.Role.AUDIENCE){ // 
                
                return
            }
            this.publish();

            
        } catch (error) { 
            
            if(/cause: -523|cause:-523/.test(error.errorMessage)){ // 
                this.setState({ 
                    talker_is_full: true, 
                    loading:false,
                    talker_full_btn_disable: true
                });
                return
            }

            let err_o = {
                '-530':'session',
                '-531':'',
                '-532':'',
                '-533':'session',
                '-534':'',
                '-200':'',

            }
            message.error(err_o[error.error] || error.errorMessage || error.message) // errorMessage:  messagejs  
            this.setState({ loading:false });

        }
    }
    join_handle = (e) => {

        e && e.preventDefault();
        
        var _this = this;
        this.props.form.validateFields((err, values) => {
            let { 
                roomName,
                nickName,
                audio, 
                video
             } = values;

            _this.setState({
                roomName,
                audio,
                video,
                nickName
            },() => {
                if (!err) {
                    _this.join()
                }
            })
        });
    }
    talker_is_full_handle() { // 
        
        this.setState({
            join_as_audience: true,
            user_room: {
                role: 1
            }
        }, this.join_handle)

    }
    // join fun end

