    // 
    hide_leaveConfirmModal() {
        this.setState({ leaveConfirmModal_show: false})
    }
    get_leaveConfirmModal() {
        let { leaveConfirmModal_show } = this.state;
        if(!leaveConfirmModal_show) return '';
        
        let { confrId } = this.state.user_room;

        return <div className="leave-confirm-modal" >
                <div className="toast-title">
                    <span></span>
                    <Icon type="close-circle"  onClick={this.hide_leaveConfirmModal} />
                </div>
                <div className="text">
                    <br></br>
                </div>
                <div className="handle">
                    <span className="leave" onClick={this.leave}></span><br />
                    <span className="end" onClick={() => emedia.mgr.destroyConference(confrId)}></span>
                </div>
            </div>
    }
    leave_handle() {

        let { role } = this.state.user_room;

        if(role == 7) {
            this.setState( state => ({leaveConfirmModal_show: !state.leaveConfirmModal_show}) )
        } else {
            let is_confirm = window.confirm('');
    
            if(is_confirm){
                this.leave()
            }
        }

    }
    leave(){
        let { am_i_white_board_creator,shared_desktop } = this.state;
        if(am_i_white_board_creator) {
            this.emit_white_board_is_destroyed()
            this.destroy_white_board()
        }
        if(shared_desktop) {
            Modal.info({
                title: ''
            });
            return
        }

        setTimeout(emedia.mgr.exitConference,300) //  
    }

    publish() {
        let { audio, video }  = this.state;
        // video = { //  video 
        //     width: {
        //         exact: 1280
        //     },
        //     height: {
        //         exact: 720
        //     }
        // }
        let constraints = { audio, video };
        if(!audio && !video) { // mic  camera  publish
            constraints = { audio: true }
        }

        let _this = this;
        (async function() {
            try {
                const stream = await emedia.mgr.publish(constraints);
                notification_show('success', '');
                if(!audio && !video) { // getUserMedia 
                    _this.close_audio()
                }
            } catch (error) {
                console.error('',error);
                notification_show('error', '');
                if(error.error == -201) {
                    notification_show('error', '');
                }
            }
        })()

    }
    // 
    apply_talker() {

        let { confrId } = this.state.user_room;
        message.success('');
        emedia.mgr.requestToTalker(confrId)

    }
    handle_apply_talker(applicat, agreeCallback, refuseCallback) {

        
        let { memberId, nickName } = applicat; //

        if(!memberId){
            return
        }
        const { confirm } = Modal;

        confirm({
            title:`${nickName || memberId}`,
            onOk: () => agreeCallback(memberId, to_audience_modal), // sdk , to_audience_modal: 
            onCancel: () => refuseCallback(memberId),
            cancelText:'',
            okText:''
        });

        let _this = this;
        const to_audience_modal = () => { //

            confirm({
                title:'',
                cancelText:'',
                okText:'',
                onOk() {
                    _this.to_audience_list.show(() => agreeCallback(memberId)) // 
                },
                onCancel: () => refuseCallback(memberId)
            })
        }

    }
    // 
    async apply_audience() {

        let { stream_list, am_i_white_board_creator, shared_desktop } = this.state;
        
        if(am_i_white_board_creator){ // 
            message.warn('');
            return
        }

        if(stream_list.filter(item => item).length == 1){ //  
            message.warn('');
            return
        }
        let { username:my_username } = this.state.user;
        let { confrId } = this.state.user_room;

        if(!my_username) {
            return
        }
        let memName = appkey + '_' + my_username;

        let _this = this;
        Modal.confirm({
            title: '',
            okText: '',
            cancelText: '',
            onOk: async () => {
                try {
                    await emedia.mgr.degradeRole(confrId, [memName], emedia.mgr.Role.AUDIENCE);
                    if(shared_desktop) {
                        _this.stop_share_desktop()
                    }
                } catch (error) {
                    
                }
            }
        });


    }
    
