/**
 *  -- 
 * @method degradeRole
 * @param {string} confrId - id
 * @param {Array} members - 
 * @param {string} role - 
 */
_outer.degradeRole = function(confrId, members, toRole) {
    if(!confrId) {
        return
    }
    if(
        !members ||
        members instanceof Array == false ||
        members.length == 0
    ) {
        console.warn('members is must Array also length > 0');
        return
    }

    if(!toRole){
        return
    }

    let confr = this._confrs[confrId];

    if(!confr) {
        console.warn('confr is not exist');
        return
    }
    let { role:oldRole, roleToken } = confr;

    if(Number(toRole) >= Number(oldRole)){ //
        console.warn('only degradeRole,oldRole: ' + oldRole + ' >>> toRole: '+ toRole);
        return
    }

    let params = {
        type:'POST',
        url: emedia.config.restPrefix + '/easemob/rtc/role/degrade',
        data: JSON.stringify({
            roleToken,
            uids:members,
            role:toRole
        }),
        contentType: 'application/json',
    }
    return new Promise((resolve, reject) => {
        $.ajax({
            ...params,
            success(data) {
                if(data.error == 0){
                    resolve(data)
                } else {
                    reject(data)
                }
                
            },
            error(error) {
                reject(error)
            }
        })
    })
}
