// setConference and deleteConference
function _update_confr_attrs(options) {
    if(
        !options || 
        Object.keys(options).length == 0
    ){
        console.warn('options is not have')
        return
    }
    if(!options.key){
        console.warn('options.key is not have')
        return
    }

    let {
        key,
        val,
        success,
        error,
        type
    } = options;

    if(!this.__current_confrId) {
        console.warn('please set_confr_attrs after joined')
        return
    }
    let { roleToken } = this._confrs[this.__current_confrId];

    
    if(!roleToken) {
        console.warn('roleToken is not have')
        return
    }
    

    let data = {
        roleToken,
        cattrs: [{
            key,
            val,
            op: type
        }],
    };
    
    $.ajax({
        type:'POST',
        url: emedia.config.restPrefix + '/easemob/rtc/confr/attrs/update',
        data: JSON.stringify(data),
        contentType: 'application/json',
        success(data) {
            if(success && typeof success == 'function'){
                success(data)
            }
        },
        error(err) {
            if(error && typeof error == 'function'){
                error(err)
            } 
        }
    })
}

/**
 * 
 * @method setConferenceAttrs
 * @param {Object} option
 * @param {string} option.key - 
 * @param {string} option.val - 
 * @param {function} option.success - 
 * @param {function} option.error - 
 */
_outer.setConferenceAttrs = function(options) {
    options.type = 'UPDATE';

    _update_confr_attrs.call(this, options)
}

/**
 * 
 * @method deleteConferenceAttrs
 * @param {Object} option
 * @param {string} option.key - 
 * @param {string} option.val - 
 * @param {function} option.success - 
 * @param {function} option.error - 
 */
_outer.deleteConferenceAttrs = function(options) {
    options.type = 'DEL';

    _update_confr_attrs.call(this, options)
}
/**
 * 
 * @method onConfrStateUpdated
 * @param {Object} confr_state - 
 */
_outer.onConfrStateUpdated = function(confr_state) {

    // 
    let _this = this;
    const mute_all_action = () => {
        let { muteAll } = confr_state;

        if(muteAll === undefined){
            return
        }

        if(muteAll) {
            if(
                _this.onMuteAll &&
                typeof _this.onMuteAll == 'function'
            ) {
                _this.onMuteAll()
            }
        } else {
            if(
                _this.onUnMuteAll &&
                typeof _this.onUnMuteAll == 'function'
            ) {
                _this.onUnMuteAll()
            }
        }
    }

    //  
    let confrId = this.__current_confrId;
    if(!confrId) {
        return
    }

    let service = this._services[confrId];
    if(!service) {
        return
    }

    let { current } = service;
    if(!current) {
        return
    }


    if(current.role && current.role != this.Role.ADMIN){ // 
        mute_all_action();
    }

} 
