 /**
 * 
 * @method joinRoom
 * @param {Object} option
 * @param {string} option.roomName - 
 * @param {string} option.password - 
 * @param {string} option.role - 
 * @param {Object} option.config -  rec: recMerge:supportWechatMiniProgram
 */
_outer.joinRoom = async function(options){
    
    //  room 
    const create = create_params => {
        let { restPrefix, appkey } = emedia.config;

        if(!appkey) {
            throw new Error('in joinRoom appkey is not defined');
        }
        let org_name = appkey.split('#')[0];
        let app_name = appkey.split('#')[1];

        let url = `${restPrefix}/${org_name}/${app_name}/conferences/room`;
        return new Promise((resolve, reject) => {
            let ajax_options = {
                url,
                data:JSON.stringify(create_params),
                type:'POST',
                contentType: 'application/json',
                success: result => {
                    if(!result.error){
                        resolve(result)
                    }else{
                        reject(result)
                    }
                },
                error: error => {
                    reject(error)
                }
            }
            $.ajax(ajax_options)
        })
    }

    let _this = this;
    // get_ticket   ticket
    const get_ticket = get_ticket_params => {

        let url = emedia.config.restPrefix + "/easemob/rtc/req/ticket" + "?_ct=" + (new Date().getTime());
        if(get_ticket_params.region) {
            url += `&REGION=${get_ticket_params.region}`
        }
        if(get_ticket_params.confrId) {
            url += `&CONFRID=${get_ticket_params.confrId}`
        }

        let params = {
            type: 'POST',
            url,
            data: JSON.stringify(get_ticket_params),
            contentType: 'application/json',
            dataType: 'json',
        }

        return new Promise((resolve, reject) => {
            $.ajax({
                ...params,
                success(data) {
                    if(data.error == 0){
                        resolve(data)
                    } else {
                        reject(data)
                    }
                    
                },
                error(error) {
                    reject(error)
                }
            })
        })
    }

    // 
    let {
        roomName,
        password,
        role,
        config
    } = options

    let {
        identityName: memName,
        identityToken: token
    } = this;


    if(!roomName){
        throw new Error('the roomName is required at joinRoom')
    }
    if(!password){
        throw new Error('the password is required at joinRoom')
    }
    if(!memName){
        throw new Error('the memName is required at joinRoom')
    }
    if(!token){
        throw new Error('the token is required at joinRoom')
    }
    if(!role){
        throw new Error('the role is required at joinRoom')
    } 
    

    let create_params = {
        roomName,
        password,
        memName,
        token,
        ...config
    }
    if (config && config.supportWechatMiniProgram) {
        create_params.useVCodes = ['H264', 'VP8'];
    }

    const room = await create(create_params); // 

    let { confrId } = room;

    let get_ticket_params = {
        uid: memName,
        token,
        confrId,
        password: password,
        role: role,
        terminal: this._terminalInfo
    }

    let ticket_info = await get_ticket(get_ticket_params); // ticket


    //  
    ticket_info.mixed = ticket_info.type === 11 || ticket_info.type === 12;
    ticket_info.id = ticket_info.serverConfrId = ticket_info.confrId = confrId;
    this._confrs[confrId] = Object.assign(this._confrs[confrId] || {}, ticket_info);


    let { ticket } = ticket_info;
    
    let { nickName, ext } = config ? config : {};
    // join confrence  ticket 
    let {isCreator} = room;
    let user_room = await _this.joinConferenceWithTicket(confrId, ticket, { nickName, ...ext });

    if(isCreator){// 
        user_room.isCreator = isCreator
    }

    return user_room
}

