
import __event from '../event';
/**
 * this.data Array
 * [{
 *  type		0121
 *  counterId		ID
 *  appCpuUsage		cpu0-100
 *  systemCpuUsage		cpu0-100
 *  streamType		,01
 *  subMemName		memNameisPubfalse
 *  subMemId		memIdisPubfasle
 *  sId		ID
 *  rtcId		rtcIdsdk
 *  isPub	BOOL	
 *  audioInputLevel		isPubtrue
 *  time		utc
 *  vKBps		kbpsisPubturefalse
 *  vFps		fpsisPubturefalse
 *  vW		isPubturefalse
 *  vH		isPubturefalse
 *  vP		isPubturefalse
 *  vPL		isPubturefalse
 *  vPLR		0-100isPubturefalse
 *  aKBps		kbpsisPubturefalse
 *  aP		isPubturefalse
 *  aPL		isPubturefalse
 *  aPLR		0-100isPubturefalse
 *  vCodec		
 *  aCodec		
 * }]
 */
class UploadStats {
    constructor(emedia) {
        this.emedia = emedia; // emedia  

        this.streams = [];
        this._intervals_get = null; //  interval
        this._intervals_upload = null; //  interval --- 
        this.data = [];
        this.counterId = 1;

        
        this.prev_obj = {
            //  store videoaudio of prev package package_lost; sent and receive ...
            /*
            * {
                [sid]: {
                    time:,
                    audio: {
                        bytes:56780,
                        packets: 890,
                        packetsLost: 2

                    },
                    video: {
                        bytes:56780,
                        packets: 890,
                        packetsLost: 2
                    }
                }
            }
            */
        }

        this.sessionId = null;

        this.network_quality_obj = {
            /**
             * [sId]: {
             *  RTT: { time, value }
             *  aPLR: { time, value}
             *  vPLR: { time, value}
             *  disconnect: { time }
            //  *  status: 0 | 1 | 2 , 0: 1: 2:  
             * }
            */
        }
        this.network_quality_threshold = { //  ,  time  value
            RTT: {
               time: 8000, // ms
               value: 500 // ms
            },
            aPLR: {
               time: 8000, // ms
               value: 18 // %
            },
            vPLR: {
               time: 8000, // ms
               value: 23 // %
            },
            disconnect: { time: 5000 } // ms
        },

        this.re_timer_count = 0; // 

    }
    _get_url() {

        let url = '';
        let { current } =  this.emedia.useCurrentXService;

        if(!current) {
            return url
        }

        let { qoeUpUrl, memName } = current.ticket;
            memName = memName.split('_')[1];

        let { appkey } = emedia.config;
        let orgName = appkey.split('#')[0],
            appName = appkey.split('#')[1];

        let ts = new Date().getTime();
        let access_token = emedia.mgr.identityToken;

        url = `${qoeUpUrl}?orgName=${orgName}&appName=${appName}&memName=${memName}&ts=${ts}&access_token=${access_token}`;

        return url

    }


    connect_ws() { // websocket

        let url = this._get_url()
        if(!url) {
            console.error('upload stats not have ws url');
            return
        }

        var socket = new WebSocket(url);
        let _this = this;
        socket.onopen = () => {
            console.log('upload stats ws connected');
            _this.ws_connected = true;
            _this.socket = socket;

            if(_this.re_connect) {// 
                console.log('has Reconnectioned');

                console.log('before joinS');
                if(_this.sessionId) {
                    console.log('have sessID');
                    _this.join_success(_this.sessionId)
                }
            }
        }

        socket.onclose = (error) => {
            _this.stop();
            _this.ws_connected = false;
            console.error('upload stats ws close');

            if(navigator.onLine ) _this.reconnection(); // 
        }

        socket.message = (evt) => {
            console.log('upload stats ws recv msg', evt);
        }

        socket.onerror = (error) => {
            console.error('upload stats ws error', error);
        }

        window.addEventListener("online", () => {
            _this.re_timer_count = 0;
            _this.reconnection()
        });
        
    }

    //  Reconnection
    reconnection() {

        if(this.ws_connected) return;

        let _this = this;
        this.reconnection_timer = setInterval(() => {
            if(_this.ws_connected || _this.re_timer_count >= 10) {
                clearInterval(_this.reconnection_timer);
            } else {
                console.log('reconnection...');
                _this.re_connect = true;
                _this.connect_ws()
                _this.re_timer_count++;

            }
        },5000)
    }


    start() {
        //  -- 
        let _this = this;
        this._intervals_get = setInterval(
            _this.get.bind(this),
            2000
        );
        this._intervals_upload = setInterval(
            _this.upload.bind(this),
            500
        )
    }

    join_success(sessionId) { //  stats

        if(!sessionId) {
            return
        }

        this.sessionId  = sessionId;
        this.data.unshift( this._get_init_data() );

        this.start()

    }

    join_fail() { //  msg

        let _o = {
            type:0,
            result: 1,
            errorInfo: 'join fail'
        }

        // 
        if(this.socket) {
            this.socket.send(_o)
        }
    }
    
    get() { // 
        if(
            !this.emedia.useCurrentXService ||
            !this.emedia.useCurrentXService.current || 
            !this.emedia.useCurrentXService.current._cacheStreams
        ) {
            return  // no streams return
        }
        
        let streams = this.emedia.useCurrentXService.current._cacheStreams; // object { streamid: stream }

        let _this = this;
        Object.keys(streams).map(key => {
            _this.push_stats_to_data(streams[key])
        })
    }

    upload() { // 

        if(this.data.length == 0) {
            console.warn('upload_stats upload fun -- this.data.length == 0');
            return
        }

        if(!this.socket) { // , 
            return
        }

        const item =  this.data.shift(); // 
        
        if(item.counterId == undefined) { //  data  counterId 
            item.counterId = this.counterId
            this.counterId = this.counterId + 1; // 
        } 
        
        this.socket.send(JSON.stringify(item));



        // display html start
        if(item.type == 1) {
            let el = document.querySelector(`#${item.sId}`);

            if(el) {
                let str = '';
                Object.keys(item).map(key => {
                    str += `<div>${key} : ${item[key]}</div>`
                })
                el.innerHTML = str;
            }
        }
        // display html end
    }

    // 
    upload_event(evt) {
        if(!evt) { 
            return
        }

        let event_obj = this._get_format_event_obj(evt);

        if(!event_obj) {
            return
        }

        this.data.splice(1, 0, event_obj); // send event frist , but init data must array[0]

    }
    // format event
    _get_format_event_obj(evt) {

        let _o = {
            type: 2,
            time: new Date().getTime()
        };

        if(
            typeof evt.event != 'object' && //   event obj ignore
            evt.event != undefined
        ) { //  EventsObservable.js 

            return Object.assign(_o, evt)
        }

        let sessionId = this.sessionId;

        if(evt instanceof __event.ICEConnected){

            let obj = this._get_ice_connect_state_obj(evt.webrtc, 2)
            _o = Object.assign( _o, obj );
        } else if(evt instanceof __event.ICEConnectFail){
            let obj = this._get_ice_connect_state_obj(evt.webrtc, 4)
            _o = Object.assign( _o, obj );
        } else if(evt instanceof __event.ICEDisconnected){ //ICE
            let obj = this._get_ice_connect_state_obj(evt.webrtc, 5)
            _o = Object.assign( _o, obj );
        }else if(evt instanceof __event.PushSuccess){

            if(!evt.hidden) { // hidden: false 
                return undefined
            }

            let {
                rtcId,
                voff,
                aoff,
                // w: ???
                // h: ???

                type: streamType,
                
            } = evt.stream;

            let obj = {
                event:14,
                sessionId,
                rtcId,
                voff,
                aoff,
                streamType,
                result: 0,
                // vcode: ???
            }

            _o = Object.assign( _o, obj )

        } else if(evt instanceof __event.SubSuccess){


            let {
                rtcId,
                id: sId,
                type: streamType,
                memId: subMemId,
                memName: subMemName,
                
            } = evt.stream;

            let obj = {
                event: 16,
                rtcId,
                sId,
                sessionId,
                result: 0,
                streamType,
                subMemName,
                subMemId
            };

            _o = Object.assign( _o, obj )

        } else if(evt instanceof __event.EnterSuccess){
            let obj = {
                event: 0,
                ip: evt.ip,
                // hopeVCode: ???
            }

            _o = Object.assign( _o, obj )
        } 


        if(_o.event == undefined) { // filter not use
            return undefined
        }
        return _o;
    }

    _get_ice_connect_state_obj(webrtc, state) {
        // adopt webrtc get stream
        let { current } =  this.emedia.useCurrentXService;

        var stream = null;
        
        for(var sid in current._linkedStreams){
            
            if(current._linkedStreams[sid].rtcId == webrtc.getRtcId()){
                stream = current._linkedStreams[sid]
            }
        }

        if(!stream) {
            return {}
        }

        let _o = {
            event:20,
            rtcId: stream.rtcId,
            connState: state,
            isPub: stream.located(),
            sId: stream.id,
            rtcCfg: stream.rtcCfg,
            voff: stream.voff,
            aoff: stream.aoff	
        };

        if(!stream.located()) { // 
            let sub_o = {
                subSId: stream.id,
                subMemName: stream.memName,
                subMemId: stream.memId,
                subVideo: stream.subSVideo,
                subAudio: stream.subSAudio
            }

            _o = Object.assign(_o, sub_o)
        }

        return _o;

    }

    // 
    stop() {
        clearInterval(this._intervals_get);
        clearInterval(this._intervals_upload);
        
    }





    async push_stats_to_data(stream) { // stats  data 

        if(!stream._webrtc){
            return
        }
        const stats = await stream._webrtc._rtcPeerConnection.getStats();
        const upload_obj = this._get_format_upload_obj(stats, stream);

        this.data.push(upload_obj);

    }

    _get_init_data() { // 
        let _o = {}

        let currentXService = this.emedia.useCurrentXService.current;
        if(!currentXService) {
            return
        }

        let { appkey, version: sdkVersion } = this.emedia.config,
            { confrId, _memberId: memId, memName } = currentXService,
            sessionId  = this.sessionId,
            { browser: deviceInfo, browserVersion: osVersion, isWifi} = this.emedia;

        _o = {
            type: 0,
            counterId: 0,
            appkey,
            confrId,
            memId,	//	memIdresult0
            memName,
            time: new Date().getTime(),
            joinConfrTime: currentXService.ticket.timestamp, 	//	result0
            sessionId, //	sessionIDresult0
            networkType:'WIFI',	//	WIFILAN4G
            sdkOS:'Web',
            sdkVersion,	
            deviceInfo,	
            osVersion,
            result:0	//	0
        }

        return _o
    }
    //  stats  stream   
    _get_format_upload_obj(stats, stream) { //  stats 
        /**
         * stats webRtc 
         * stream_info : sIdmemId ...
         * samplehttps://webrtc.github.io/samples/src/content/peerconnection/constraints/
         */

        // sentreceived
        let _o = {
            type: 1, 
            time: new Date().getTime(),
            streamType: stream.type,
            sId: stream.id,
            rtcId: stream.rtcId
        };

        if(stream.located()) { // 
            _o.isPub = true;
            const outbound_stats_obj = this._get_outbound_stats(stats);

            _o = Object.assign(_o, outbound_stats_obj)
        } else { // 
            _o.isPub = false;
            _o.subMemName = stream.memName;
            _o.subMemId = stream.memId;
            
            const inbound_stats_obj = this._get_inbound_stats(stats);

            _o = Object.assign(_o, inbound_stats_obj)
        }

        //  
        _o = this._calc_KBps_and_PLR(_o);

        //   data  int
        _o = this._set_stats_int(_o);

        return _o
    }
    
    // 
    _get_outbound_stats(stats) {
        let _o = { };

        stats.forEach(item => {

            if(item.type == 'outbound-rtp') {
                if(item.mediaType == 'audio') {
                    _o.aP = item.packetsSent; 

                    _o.aB = item.bytesSent; // 
                   
                }
                if(item.mediaType == 'video') {
                    _o.vW = item.frameWidth
                    _o.vH = item.frameHeight
                    _o.vP = item.packetsSent

                    _o.vB = item.bytesSent; // 
                }
                
            }

            if(item.type == 'remote-inbound-rtp') { // get packetlost
                if(item.kind == 'audio') {
                    _o.aPL = item.packetsLost
                }
                if(item.kind == 'video') {
                    _o.vPL = item.packetsLost
                }
            }

            if(item.type=='media-source') {

                if(item.kind=='audio') {
                    _o.audioInputLevel = item.audioLevel*1000
                }

                if(item.kind == 'video'){
                    _o.vFps = item.framesPerSecond
                }

            }

            if(item.type == 'candidate-pair') {
                _o.RTT = (item.totalRoundTripTime*1000)/item.responsesReceived // totalRoundTripTime: s ---> ms 
            }

        });

        
        return _o;
    }
    
    // 
    _get_inbound_stats(stats) {
        let _o = { }
        stats.forEach(item => {

            if(item.type == 'inbound-rtp') {
                if(item.kind == 'audio' || item.id.indexOf('AudioStream')>-1) { // safiri
                    _o.aP = item.packetsReceived;
                    _o.aPL = item.packetsLost;

                    _o.aB = item.bytesReceived // 
                }

                if(item.kind == 'video' || item.id.indexOf('VideoStream')>-1) { // safiri
                    _o.vP = item.packetsReceived;	
                    _o.vPL = item.packetsLost;

                    _o.vB = item.bytesReceived // 
                }
                
            }

            if(item.type == 'track') {
                if(item.kind == 'video') {
                    _o.vW = item.frameWidth
                    _o.vH = item.frameHeight

                    _o.vFR = item.framesReceived // 
                }

                if(item.kind == 'audio') {
                    _o.audioInputLevel = item.audioLevel*1000 //  0
                }
            }

            if(item.type == 'candidate-pair') {
                _o.RTT = (item.totalRoundTripTime*1000)/item.responsesReceived // totalRoundTripTime: s ---> ms 
            }

        });

        return _o;
    }

    // 
    _calc_KBps_and_PLR(upload_obj) {


        let {
            sId,
            aB,
            vB,
            time
        } = upload_obj;

        // 

        upload_obj.aKBps = this._get_KBps_by_sId(sId, 'audio', aB, time) || 0;
        upload_obj.vKBps = this._get_KBps_by_sId(sId, 'video', vB, time) || 0;

        // 
        let {
            aP,
            aPL,
            vP,
            vPL
        } = upload_obj;

        let aPLR = this._get_PLR_by_sId(sId, 'audio', aP, aPL);
        let vPLR = this._get_PLR_by_sId(sId, 'video', vP, vPL);

        if(aPLR || aPLR == 0) {
            upload_obj.aPLR = aPLR;
        }

        if(vPLR || vPLR == 0) {
            upload_obj.vPLR = vPLR;
        }

        // 
        if(!upload_obj.isPub){
            let {
                vFR
            } = upload_obj;

            if(
                this.prev_obj[sId] &&
                this.prev_obj[sId]['video'] &&
                this.prev_obj[sId]['video']['framesReceived']
            ) {
                let prev_time = this.prev_obj[sId].time;
                let prev_vFR = this.prev_obj[sId]['video']['framesReceived'];

                let interval = (time-prev_time)/1000 // s
                let vFps = (vFR - prev_vFR)/interval;
                if(vFps<0) { vFps = 0 };
                upload_obj.vFps = vFps;
            }

        }
        // 
        // const network_quality = this._network_quality(upload_obj);
        // const network_disconnect = this._network_disconnect(upload_obj);
        const network_quality = this._get_w_quality(upload_obj);

        if(network_quality != 2){

            let event_obj = {
                time: new Date().getTime(),
                event: 4,
                type:2,
                counterId: this.counterId,
                sessionId: this.sessionId,
                rtcId: upload_obj.rtcId,
                sId: upload_obj.sId,
                isPub: upload_obj.isPub,
                streamType: upload_obj.sId,
                aoff: upload_obj.aoff,
                voff: upload_obj.voff,
                subMemName: upload_obj.subMemName,
                subMemId: upload_obj.subMemId
            }

            this.data.push(event_obj); // 

        }
        
        


        // 
        if(
            this.emedia.mgr &&
            this.emedia.mgr.onNetworkQuality
        ) {
            let callback = this.emedia.mgr.onNetworkQuality;

            if(typeof callback == 'function') {
                callback(upload_obj.sId, network_quality)
            }
        }

        


        // 
        this._set_prev_obj(upload_obj);

        //  aBvBvFR 
        delete upload_obj.aB; // audio bytesSent or bytesReceived
        delete upload_obj.vB; // video bytesSent or bytesReceived
        delete upload_obj.vFR; // video framesReceived 
        delete upload_obj.RTT; // RTT 

        return upload_obj
    }

    _set_stats_int(_o) { // stats  

        let need_int_keys = [
            'audioInputLevel','vKBps','vFps','vW','vH',
            'vP','vPL','vPLR','aKBps','aP','aPL','aPLR'
        ]
        for (const key in _o) {
            if(need_int_keys.indexOf(key) > -1) {
                _o[key] = Math.round(_o[key])
            }
        }

        return _o
    }
    

    //    sId
    _get_KBps_by_sId(sId, kind, bytes, time) {

        if(!sId || !kind || !bytes) {
            return null
        }

        let prev_info = this.prev_obj[sId];

        if(
            !prev_info ||
            !prev_info[kind] ||
            !prev_info[kind]['bytes']
        ) { //  
            return null
        }

        let { time: prev_time } = prev_info;
        let {
            bytes: prev_bytes
        } = prev_info[kind];

        var interval = parseInt(time - prev_time) //  ms

        var increment = bytes - prev_bytes;

        if(increment < 0) { //    0 
            increment = 0
        }

        var bytes_rate = increment/(interval/1000).toFixed(2) //  S 

        return bytes_rate/1000 // KBps
    }

    /**
     * 
     *  --  /(+)
     * kind: video or audio
     * end_type: sent or received
     * package_num : total of package sent or receive, 
     * package_loat_num: total of package lost  sent or receive
     * */ 
    _get_PLR_by_sId(sId, kind, packets, packets_lost) {

        if(!sId) {
            return null
        }

        if(
            !kind ||
            !packets || 
            !packets_lost
        ) {
            return 0
        }


        let prev_info = this.prev_obj[sId];

        if(
            !prev_info ||
            !prev_info[kind] ||
            !prev_info[kind]['packets'] ||
            !prev_info[kind]['packets_lost']
        ) {
            return 0
        }

        let {
            packets: prev_packets,
            packets_lost: prev_packets_lost
        } = prev_info[kind];


        let packets_increment = packets-prev_packets,
            packets_lost_increment = packets_lost-prev_packets_lost;

        let packets_lost_rate = packets_lost_increment/(packets_increment+packets_lost_increment);
            packets_lost_rate = Math.round(packets_lost_rate*100)

        return packets_lost_rate;

    }

    
    // 
    _set_prev_obj(_o) {

        let { sId } = _o;
        if(!sId) {
            return
        }
        
        let {
            aB,
            vB,
            time,
            aP,
            aPL,
            vP,
            vPL,
            vFR
        } = _o;



        let audio = {}, video = {};

        if(aB) {
            audio.bytes = aB
        }
        if(aP) {
            audio.packets = aP
        }
        if(aPL || aPL == 0) {
            audio.packets_lost = aPL
        }
        // video
        if(vB) {
            video.bytes = vB
        }
        if(vP) {
            video.packets = vP
        }
        if(vPL || vPL == 0) {
            video.packets_lost = vPL
        }
        if(vFR) {
            video.framesReceived = vFR
        }

        if(
            Object.keys(audio).length == 0 &&
            Object.keys(video).length == 0
        ) { // 
            return 
        }

        if(!this.prev_obj[sId]) {
            this.prev_obj[sId] = {}
        }
        this.prev_obj[sId].time = time;

        if(
            Object.keys(audio).length > 0
        ) { 
            this.prev_obj[sId].audio = audio;
        }

        if(
            Object.keys(video).length > 0
        ) { 
            this.prev_obj[sId].video = video;
        }
        
    }


    //  0  1  2 
    _get_w_quality(upload_obj) {
        let {
            RTT,
            aPLR,
            vPLR,
            time,
            vKBps, 
            aKBps,
            sId
        } = upload_obj;

        if(!sId) {
            return 2
        }


        let _this = this;
        // start calc disconnect
        const is_dis = () => {
            let sId_info = _this.network_quality_obj[sId];

            //  vKBps, aKBps   
            if(!sId_info) { // 
                _this.network_quality_obj[sId] = { disconnect: { time } };
                return false
            }

            if(!sId_info['disconnect']) { // , 
                _this.network_quality_obj[sId] = { disconnect: { time } };
                return false
            }

            

            let frist_time = sId_info['disconnect'].time;

            let timeout = _this.network_quality_threshold['disconnect'].time;

            if(time - frist_time >= timeout) {// 
                return true
            }
            
            return false
            //  
            // time 
            // s --> f_dis_time  
            // dis_timeout 
            
        }

        // stop disconnect
        const reset_dis_time = () => {
            let sId_info = _this.network_quality_obj[sId];
            if(!sId_info || !sId_info['disconnect']) return; //

            _this.network_quality_obj[sId]['disconnect'] = undefined;
        } 


        // start calc weak
        // 

        const is_weak = (type) => {
            if(
                !type ||
                !time
            ) {
                return false
            }

            let sId_info = _this.network_quality_obj[sId];

            if(!sId_info) { //  type
                _this.network_quality_obj[sId] = { [type]: { time } }

                return false
            }
            if(!sId_info[type]) { //  type
                _this.network_quality_obj[sId][type] = { time }
                return false
            }

            
            
            let frist_time = sId_info[type].time;
            let timeout = _this.network_quality_threshold[type].time;

            // timeout  true
            if(time - frist_time >= timeout) {
                return true
            }
        

            return false;
        }
        // stop weak
        const reset_weak_tiem = (type) => {
            let sId_info = _this.network_quality_obj[sId];

            if(!sId_info || !sId_info[type]) return; //

            _this.network_quality_obj[sId][type] = undefined;
        }
        // good
        
        // vKBps=0,aKBps=0;
        

        if(!vKBps && !aKBps) {
            if(is_dis()){
                return 0
            }
            
            return 2
        }else {
            reset_dis_time();
            
            let checks = ['RTT', 'aPLR', 'vPLR'];// 
            let weak = 2;

            for (let index = 0; index < checks.length; index++) {
                const type = checks[index];
                let threshold = this.network_quality_threshold[type].value;

                
                if(upload_obj[type] >= threshold){// 
                    if(is_weak(type, time)) weak = 1;
                } else {
                    reset_weak_tiem(type)
                }
                
            }

            return weak;
        }

    }

}
export default UploadStats